              size="lg"
            />
          </b-col>
          <b-col
            cols="12"
            md="4"
            class="mb-md-0 mb-2 pb-2"
          >
            <label class="saj-text">{{ $t("Category") }}:</label>
            <b-form-select
              v-model="selectedCategory"
              :options="categoryList"
              :value="categoryList"
              size="lg"
              class="saj-text"
            />
          </b-col>
          <b-row
            cols="12"
            class="flex-row flex-column mr-1 pl-1"
