          <template #cell(name)="data">
            <div class="saj-text d-flex justify-content-center">
              {{ data.item.category }}
            </div>
          </template>
          <template #cell(category)="data">
            <div class="saj-text d-flex justify-content-center">
              {{ data.item.category }}
            </div>
          </template>
          <template #cell(action)="">
