import {
  BCard,
  // BCardHeader,
  // BCardBody,
  BRow,
  BCol,
  BTable,
  BButton,
  BFormInput,
  BFormSelect,
  VBPopover,
  BPopover,
} from "bootstrap-vue"
import Ripple from "vue-ripple-directive"

export default {
  components: {
    BRow,
    BCol,
    BCard,
