          employee_number: '001',
          employee_name: 'test1',
          category: '1',
        },
      ],
      fields: [
        {
          key: 'employee_number', label: 'Id', thClass: 'text-center',
        },
        {
          key: 'employee_name', label: 'Name', thClass: 'text-center',
        },
        {
          key: 'category', label: 'Category', thClass: 'text-center',
        },
        {
          key: 'action', label: 'Action', thClass: 'text-center',
        },
      ],
    }
  },
  computed: {
  },
  mounted() {
  },
  methods: {
  },

}
</script>
<style>
.table th, .table td {
    text-align: center;
    color: black;
}
</style>
